html {
    background-color: #696969
}

.App {
    text-align: center;
    background-color: #696969;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

/* .App-link {
    color: #61dafb;
} */

svg g {
    transform: translate(50%, 50%);
}

.circles {
    font-size: 0.5em;
    height: 100vh;
    width: 100vw;
}

.about {
    position: relative;
}

.about::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    transform: translateX(-50%);
    background-color: white;
    transition: width 0.25s ease-in-out;
}

.about:hover::after {
    width: 100%;
}

.currentPage {
    position: relative;
}

.currentPage::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 100%;
    height: 2px;
    transform: translateX(-50%);
    background-color: white;
}

.navigation {
    width: 100%;
    height: 75px;
    background-color: black;
    box-shadow: 2px 2px 4px #454545;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.start {
    width: 100%;
    display: flex;
    align-items: left;
    justify-content: space-between;
}

.start a {
    margin-left: 15px;
    color:white;
    text-decoration: none;
    margin-right: 15px;
}

.links {
    position: relative;
    width: 70%;
    margin: 0;
    margin-right: 35px;
    padding: 0; 
    display: flex;
    justify-content: flex-end;
}

.links a {
    height: 40px;
    display: inline-block;
    text-align: center;
    color: white;
    text-decoration: none;
    margin-left: 10px;
    margin-right: 10px;
}

.links a ul {
    display: none;
    list-style: none;
    position: absolute;
    margin-top: 0.8px;
    padding: 0px 0px 0px 0px;
    color: black;
    background-color: lightgray;
    border-radius: 0px 0px 8px 8px;
    text-align: justify;
}

.links a ul a {
    display: inline-flex;
    position: absolute;
    font-size: 10px;
}

.links a:hover ul {
    display: block;
}

.links ul li:hover{
    background-color: lightblue;
    border-radius: 0px 0px 8px 8px;
}

.blockSatz {
    text-align: justify;
    hyphens: auto;
    margin-left: 15px;
    margin-right: 20px;
}
